import React, { useState, useEffect, useContext } from "react";
import { Line } from "react-chartjs-2";
import { TestContext } from "../TestContext";
import { rainbow } from "./../util/rainbow";
import _ from "lodash";

export function UsersGraph() {
  const { userGroupUserCount } = useContext(TestContext);
  const [dataHistory, setDataHistory] = useState([]);

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "User Group Users",
      },
    },
    scales: {
      x: {
        type: "time",
        time: {
          source: "auto",
        },
      },
    },
  };

  useEffect(() => {
    if (!_.isEqual(dataHistory[dataHistory.length - 1], userGroupUserCount)) {
      if (dataHistory.length <= 100) {
        setDataHistory([...dataHistory, userGroupUserCount]);
      } else {
        setDataHistory([
          ...dataHistory.filter((record, index) => index % 2 === 0),
          userGroupUserCount,
        ]);
      }
    }
  }, [dataHistory, userGroupUserCount]);

  let dataSetMap = {};
  for (const { time, data } of dataHistory) {
    if (data) {
      let records = Object.entries(data).map((entry) => {
        return { label: entry[0], count: entry[1] };
      });
      for (let record of records) {
        if (!dataSetMap[record.label]) {
          dataSetMap[record.label] = [];
        }
        dataSetMap[record.label] = [
          ...dataSetMap[record.label],
          {
            x: new Date(time),
            y: record.count,
          },
        ];
      }
    }
  }

  const datasets = [];
  const labelsCount = Object.keys(dataSetMap).length;
  let i = 0;
  for (let [label, data] of Object.entries(dataSetMap)) {
    datasets.push({
      label,
      data,
      borderColor: rainbow(labelsCount, i),
      backgroundColor: rainbow(labelsCount, i),
      tension: 0,
    });
    i++;
  }

  const data = {
    datasets: datasets,
  };

  const props = {};

  return dataHistory?.length > 0 ? (
    <Line options={options} data={data} {...props} />
  ) : (
    <div></div>
  );
}
