import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { SummaryTable } from "./summary/SummaryTable.js";
import { Container, Row, Col } from "react-bootstrap";
import { useContext, useState, useEffect } from "react";
import { AverageResponseTimeGraph } from "./summary/AverageResponseTimeGraph.js";
import { UsersGraph } from "./summary/UsersGraph.js";
import { TestContext } from "./TestContext.js";
import { ThroughputGraph } from "./summary/ThroughputGraph.js";
import { SuccessRateGraph } from "./summary/SuccessRateGraph.js";

function Dashboard() {
  const { running, finished, startTime, endTime } = useContext(TestContext);
  const [clock, setClock] = useState(Date.now());

  let runningState = "-";
  if (!running && !finished) {
    runningState = "Waiting";
  } else if (running && !finished) {
    runningState = "Running";
  } else if (finished) {
    runningState = "Finished";
  }

  useEffect(() => {
    const timerId = setInterval(() => {
      setClock(Date.now());
    }, 1000);

    return function cleanup() {
      clearInterval(timerId);
    };
  }, []);

  const elapseTime = Math.floor(
    ((endTime ?? clock) - (startTime ?? clock)) / 1000
  );

  return (
    <Container>
      <Row>
        <Col>
          <div>Testing state: {runningState}</div>
          <div>
            {(startTime && `Start: ${new Date(startTime).toLocaleString()}`) ||
              ""}
          </div>
          <div>
            {(endTime && `End: ${new Date(endTime).toLocaleString()}`) || ""}
          </div>
          <div>{startTime && `Elaspsed Time: ${elapseTime}s`}</div>
          <div style={{ height: "1em" }}></div>
        </Col>
      </Row>
      <hr />
      <Row>
        <Col>
          <SummaryTable />
        </Col>
      </Row>
      <hr />
      <Row>
        <Col md={6}>
          <AverageResponseTimeGraph />
        </Col>
        <Col md={6}>
          <ThroughputGraph />
        </Col>
      </Row>
      <Row>
        <Col md={6}>
          <SuccessRateGraph />
        </Col>
        <Col md={6}>
          <UsersGraph />
        </Col>
      </Row>
    </Container>
  );
}

export default Dashboard;
