import React, { useEffect, useState, useContext, createContext } from "react";
import io from "socket.io-client";
import { TestContext } from "../TestContext";

export function SummaryDataSource(props) {
  const [socket, setSocket] = useState(null);
  const [intermediateSummary, setIntermediateSummary] = useState({});
  const [tickSummary, setTickSummary] = useState({});
  const [userGroupUserCount, setUserGroupUserCount] = useState({});
  const [running, setRunning] = useState(false);
  const [finished, setFinished] = useState(false);
  const [startTime, setStartTime] = useState(null);
  const [endTime, setEndTime] = useState(null);

  useEffect(() => {
    const newSocket = io(`http://localhost:3001/dashboard`);
    setSocket(newSocket);

    newSocket.on("master:summary:intermediate", (_intermediateSummary) => {
      setRunning(true);
      !startTime && setStartTime(_intermediateSummary.startTime);
      setIntermediateSummary(_intermediateSummary);
    });

    newSocket.on("master:summary:tick", (_tickSummary) => {
      setTickSummary(_tickSummary);
    });

    newSocket.on("master:userGroupUserCount", (_userGroupUserCount) => {
      setUserGroupUserCount(_userGroupUserCount);
    });
    newSocket.on("master:test-finished", () => {
      console.debug("master:test-finished");
      !endTime && setEndTime(Date.now());
      setRunning(false);
      setFinished(true);
    });

    return () => newSocket.close();
  }, []);

  useEffect(() => {}, [socket]);

  return (
    <TestContext.Provider
      value={{
        intermediateSummary,
        tickSummary,
        userGroupUserCount,
        running,
        finished,
        startTime,
        endTime,
      }}
    >
      {props.children}
    </TestContext.Provider>
  );
}
