import React, { useState, useEffect, useContext } from "react";
import { Line } from "react-chartjs-2";
import { TestContext } from "../TestContext";
import { rainbow } from "../util/rainbow";
import _ from "lodash";

export function AverageResponseTimeGraph() {
  const { tickSummary } = useContext(TestContext);
  const [tickHistory, setTickHistory] = useState([]);

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Avg response time",
      },
    },
    scales: {
      x: {
        type: "time",
        time: {
          source: "auto",
        },
      },
    },
  };

  useEffect(() => {
    if (!_.isEqual(tickHistory[tickHistory.length - 1], tickSummary)) {
      if (tickSummary.tickSummary) {
        if (tickHistory.length <= 100) {
          setTickHistory([...tickHistory, tickSummary]);
        } else {
          setTickHistory([
            ...tickHistory.filter((record, index) => index % 2 === 0),
            tickSummary,
          ]);
        }
      }
    }
  }, [tickHistory, tickSummary]);

  let dataSetMap = {};
  let labels = new Set();
  for (const tick of tickHistory) {
    const tickTime = tick.thisTickTime;

    // add new labels
    Object.keys(tick.tickSummary).forEach((label) => {
      if (!labels.has(label)) {
        labels.add(label);
        dataSetMap[label] = [];
      }
    });

    for (let [label, record] of Object.entries(tick.tickSummary)) {
      if (!dataSetMap[label]) {
        dataSetMap[label] = [];
      }
      dataSetMap[label].push({
        x: new Date(tickTime),
        y: record.average,
      });
    }

    // for (let label of labels) {
    //   if (!tick.tickSummary[label]) {
    //     dataSetMap[label].push({
    //       x: new Date(tickTime),
    //       y: 0,
    //     });
    //   }
    // }
  }

  const datasets = [];
  const labelsCount = Object.keys(dataSetMap).length;
  let i = 0;
  for (let [label, data] of Object.entries(dataSetMap)) {
    datasets.push({
      label,
      data,
      borderColor: rainbow(labelsCount, i),
      backgroundColor: rainbow(labelsCount, i),
      tension: 0,
    });
    i++;
  }

  const data = {
    datasets: datasets,
  };

  const props = {};

  return <Line options={options} data={data} {...props} />;
}
