import React, { useContext } from "react";
import Table from "react-bootstrap/Table";
import { TestContext } from "../TestContext";

function formatTime(time) {
  if (time < 1000) {
    return `${time} ms`;
  } else {
    return `${time / 1000} s`;
  }
}

export function SummaryTable() {
  const { intermediateSummary } = useContext(TestContext);
  const { summary } = intermediateSummary;

  let summaryRecords = (summary && Object.values(summary)) ?? [];
  summaryRecords.sort((a, b) => {
    return a.count - b.count;
  });

  let intermediateSummaryRecordElems = summaryRecords.map((record) => {
    return (
      <tr key={record.label}>
        <td>{record.label}</td>
        <td>{record.count}</td>
        <td>{record.success}</td>
        <td>{record.fail}</td>
        <td>{formatTime(+record.average?.toFixed(0))}</td>
        <td>{formatTime(+record.min?.toFixed(0))}</td>
        <td>{formatTime(+record.max?.toFixed(0))}</td>
        <td>{formatTime(record.percentile?.p25)}</td>
        <td>{formatTime(record.percentile?.p50)}</td>
        <td>{formatTime(record.percentile?.p75)}</td>
        <td>{formatTime(record.percentile?.p90)}</td>
        <td>{formatTime(record.percentile?.p95)}</td>
      </tr>
    );
  });

  return (
    <div>
      <header>Aggregate Summary</header>

      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Label</th>
            <th>Samples</th>
            <th>Success</th>
            <th>Fail</th>
            <th>Avg</th>
            <th>Min</th>
            <th>Max</th>
            <th>25p</th>
            <th>50p</th>
            <th>75p</th>
            <th>90p</th>
            <th>95p</th>
          </tr>
        </thead>
        <tbody>{intermediateSummaryRecordElems}</tbody>
      </Table>
    </div>
  );
}
